:root {
  box-sizing: border-box;
  /* CSS HEX */
  --color-primary: #3b5249ff;
  --color-secondary: #dbd8b3ff;
  --black-coffee: #34252fff;
  --tenne-tawny: #cc5803ff;
  --color-disabled: #a167a5ff;
  --color-tertiary: hsl(56, 36%, 86%);
}

input[type="checkbox"] {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
  cursor: pointer;
}

.filter-input {
  border: none;
  height: 2.0em;
  border-radius: 7px;
  padding-left: 10px;
}

.category-checkbox-container {
  display: inline-block;
  position: relative;
  top: 2px;
  padding-left: 40px;
}

.checkmark {
  position: absolute;
  top: 0px;
  left: 15px;
  height: 20px;
  width: 20px;
  border-radius: 3px;
  background-color: white;
}

h3 {
  margin: 10px 0;
}

.category-checkbox-container:hover input ~ .checkmark {
  background-color: #ccc;
}

.category-checkbox-container input:checked ~ .checkmark {
  background-color: var(--tenne-tawny);
}
/* Style the checkmark/indicator */
.category-checkbox-container .checkmark:after {
  content: '';
  left: 7px;
  top: 2px;
  width: 4px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.category-checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-primary);
  height: 70px;
}

.app-wrapper {
  max-width: 1000px;
  margin: 0 auto;
  padding: 30px;

  /* This would make the app wrapper nicely fill the whole sreen
     but doesn't work when there's more than on screen full of content. */
  /* 100% - header height - padding (top+bottom) - app padding */
  /*height: calc(100% - 70px - 60px - 30px);*/
}

.app {
  background-color: var(--color-secondary);
  border-radius: 20px;

  padding: 15px 20px;
  border: 1px solid var(--color-primary);
  height: 100%;
}

.app-heading {
  color: white;
}

/* Recipe list (aka SearchView) */
.recipe-list {
  padding: 20px 0 0 10px;
}

/*.recipe-category {

}*/

.recipe-list-item {
  padding: 2px 0;
}

.recipe-list-item:hover {
  background-color: aquamarine;
  border: 1px solid black;
}

.recipe-list-item:active {
  position: relative;
  left: 3px;
  top: 3px;
  background-color: slategray;
  border: 1px solid black;
}

.recipe-view-header {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  height: 35px;
}

button {
  color: white;
  background-color: var(--color-primary);
  border: none;
  padding: 5px 20px;
  border-radius: 5px;
  box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.2);
}

button:hover {
  padding: 7px 24px;
  position: relative;
  /* Adjust position by the added padding. Vertical position
     is handled by flexbox's align-items property. */
  left: 2px;
}

button:active {
  background-color: var(--black-coffee);
  padding: 5px 20px;
  position: relative;
}

.recipe-content {
  display: flex;
  flex-wrap: wrap;
}

/* Ingredients */
.ingredient-sections {

  margin: 10px 20px 20px 0;
  flex: 1 0 300px;
}

.ingredient-section {
  background-color: var(--color-tertiary);
  border-radius: 20px;
  padding: 10px;
  margin: 0 0 10px 0;
}

.ingredient {
  display: flex;
  padding: 1px 0;
}

.ingredient-amount {
  min-width: 2em;
  flex: 0 0 auto;
}

.ingredient-unit {
  min-width: 2em;
  flex: 0 0 auto;
}

.ingredient-name {
  flex: 1 1 auto;
}

/* Instructions */
.instruction-sections {
  background-color: var(--color-tertiary);
  border-radius: 20px;
  padding: 10px;
  margin: 10px 20px 20px 0;
  flex: 3 0 300px;
}

/*.instruction-section {}*/

.instruction-step {
  display: flex;
  padding: 3px 0px;
}

.instruction-index {
  min-width: 2em;
  flex: 0 0 auto;
}

.instruction-text {
  flex: 1 1 auto;
}
